.socialMedias {
  margin: 0;
  padding: 0; }
  .socialMedias .button {
    height: 50px;
    width: 50px;
    border-radius: 100%;
    padding: 10px;
    border: 3px solid #252525; }
    .socialMedias .button img {
      height: 30px;
      width: 30px;
      -webkit-filter: saturate(0) brightness(0.45);
              filter: saturate(0) brightness(0.45);
      -webkit-transition: -webkit-filter 0.4s;
      transition: -webkit-filter 0.4s;
      -o-transition: filter 0.4s;
      transition: filter 0.4s;
      transition: filter 0.4s, -webkit-filter 0.4s; }
  .socialMedias .button:hover {
    border: 3px solid #5543D2; }
    .socialMedias .button:hover img {
      -webkit-filter: saturate(0) brightness(4);
              filter: saturate(0) brightness(4); }
  .socialMedias .button:not(:last-child) {
    margin: 0 20px; }
  .socialMedias .button:first-child {
    margin-left: 0; }
  .socialMedias.inverse .button {
    border: 3px solid white;
    -webkit-transition: background-color 0.4s;
    -o-transition: background-color 0.4s;
    transition: background-color 0.4s; }
    .socialMedias.inverse .button img {
      -webkit-filter: saturate(0) brightness(4);
              filter: saturate(0) brightness(4); }
  .socialMedias.inverse .button:hover {
    background-color: white; }
    .socialMedias.inverse .button:hover img {
      -webkit-filter: none;
              filter: none; }



