.input {
  position: relative;
  margin-bottom: 20px;
  width: 100%; }
  .input label {
    position: absolute;
    display: block;
    color: #434343;
    font-size: 0.8rem; }
  .input input, .input textarea {
    outline: none; }
  .input input:not([type=submit]), .input textarea {
    height: 2.5rem;
    width: 100%;
    background-color: transparent;
    color: #434343;
    -webkit-transition: border-color 0.4s, color 0.4s;
    -o-transition: border-color 0.4s, color 0.4s;
    transition: border-color 0.4s, color 0.4s;
    border: none;
    border-bottom: 1px solid #434343;
    border-left: 1px solid #434343;
    border-bottom-left-radius: 3px;
    margin-top: 1.2rem;
    padding: 0 0 4px 8px;
    font-size: 1.2rem; }
  .input input[type=submit] {
    padding: 0; }
  .input textarea {
    font-size: 1.4rem;
    line-height: 1.8rem;
    width: 100%;
    height: calc(10.8rem + 0px);
    resize: none; }
  .input input:not([type=submit]):focus, .input textarea:focus {
    border-color: #7E7EFF;
    color: #252525; }



